@import url('https://fonts.googleapis.com/css?family=Roboto+Slab');
html{
	overflow-x: hidden;	
}
body{
	background-color: #eee;
	overflow-x: hidden;
}
header{
	height: 220px;
	width: 100%;
	background-color: #17313b;
	position: relative;
	z-index: 1;
}
content{
	height: auto;
	min-height: 200px;
}
footer{
	height: 400px;
	background-color: #cacaca;
	margin-top: 80px;
}
.display-none{
	display: none;
}
button{
		color: #fff;
		height: 26px;
		background-color: #17313b;
		border: none;
}

.header-all-nav{
	background-color: #0b181d;
	height: 64px;
	position: relative;
	width: 100%;
	top: 100px;
	padding: 0px;
	z-index: 2;
	
}
.header-navbar{
	padding: 0px;
	color: #fff;
	position: relative;
	width: 90%;
	height: 65px;
	
	padding-left: 42px;
	
}
.header-navbar ul li{
	float: left;
	list-style-type: none;
	margin-right: 16px;
	font-size: 16px;
	display: block;
	position: relative;
	height: 65px;
	padding: 16px;
	

}
.header-navbar ul li:hover{
	background-color: #00000033;
}
.header-navbar ul li a:hover{
	color: #fff;
	text-decoration: none;
	display: block;

}
#header-nav-click{
	display: none;
}
.header-nav-button{
	display: none;
}

.loginButton{
	color: #fff;
	float: right;
	margin: 0px;
	position: relative;
	height: auto;
	padding: 8px;
	height: 64px;
	width: 10%;
	background-color: #00000033;
}
.loginButton:hover{
	background-color: #00000088;
}
.loginButton div, .loginButton p{
	float: right;
	height: inherit;
	margin: 0px;
	width: 100%;
	padding: 6px;
}
.loginButton p{
	margin: auto;
	font-size: 16px;
}
.header{
	padding-top: 50px;
}
#login-window{
	width: 100%;
	height: 130%;
	position: fixed;
	background-color: #000000aa;
	margin: 0px;
	opacity: 0;
	z-index: -1;
	-webkit-transition: opacity 0.5s, right 0.5s;
	-o-transition: opacity 0.5s, right 0.5s;
	transition: opacity 0.5s, right 0.5s;

}
.purchase-one{
	margin-top: 8px;
	font-size: 12px;
	color: #909090;
	background-color: #f5f5f5;
	width: 100%;
	height: auto;
	overflow: hidden; 
	border-bottom: solid 1px #d9d9d9;
}
.purchase-one span{
	font-size: 14px;
	color: #000;
}
.purchase-one-head{
	font-size: 18px;
}
.purchase-one-name{
	font-size: 16px;
	color: #5a5aff;
}
.begin-price{
	font-size: 18px;
}

.purchase-one-date{
	float: left;
	width: 16%;
	padding: 8px;

}
.purchase-one-info{
	float: left;
	width: 65%;
	padding: 8px;
	color: #000;
	font-size: 14px;
}
.purchase-one-price{
	float: left;
	width: 16%;
	padding: 8px;
	height: inherit;
}
.drop-down-list{
	display: block;
	background-color: #fff;
	width: 400px;
	height: 30px;
	margin-bottom: 0px;
}
.drop-down-list input{
	display: none;
}
#search-full-click{
	display: none;
}
.drop-down-list-vis{
	height: 0px;
	overflow: hidden;
	-webkit-transition: .3s linear;
	-o-transition: .3s linear;
	transition: .3s linear;
	opacity: 1;
	width: 400px;
	overflow: scroll;
	overflow-x: hidden;
	background-color: #fff;
	position: absolute;
}
.drop-down-list-vis li, ul{
	list-style-type: none;
	padding: 0px;
}
.drop-down-list-vis li{
	height: 40px;
}
.drop-down-list-vis li:hover{
	background-color: #dadada;
}
#search-full-click:checked + .drop-down-list + .drop-down-list-vis{
	height: 200px;
	opacity: 1;
	
}
#search-full-click:checked + .drop-down-list input{
}
#login{
	position: absolute;
	opacity: 0;
	right: -2000px;
	-webkit-transition: opacity 0.3s, right 0.4s;
	-o-transition: opacity 0.3s, right 0.4s;
	transition: opacity 0.3s, right 0.4s;
}
#login-close{
	float: right;
	width: 30px;
	height: 30px;
	margin: 16px;
	background-image: url(./cross.svg);
}
#login-window #login{
	position: fixed;
	width: 55%;
	height: 130%;
	background-color: #fefefe;
	margin: 0px;
	z-index: 3;
}
.login-buttons{
	clear: left;
}
.login-buttons button{
	margin-top: 16px;
	margin-right: 16px;
}
.login-form{
	margin-top: 32px;
	margin-left: 32px;
	background-color: #fefefe;
	width: 40%;
	border-right: 3px solid #17313d;
	padding-right: 22px;
	height: auto;
	overflow: hidden;
}
.login-form h1{
	font-size: 28px;
}
.login-form input{
	float: left;
	clear: left;
	margin-top: 8px;
	width: 100%;
}
.login-form button{
	border-radius: 5px;
}
.login-checkin *{
	float:left;
	clear: left;
	margin-top: 8px;
}
.login-checkin button{
	width: 100%;
}
.header-navbar a{
	color: #fff;
}
.header-navbar a:hover{
	color: #000;
}
.search{
	padding: 32px;
	padding-left: 0px;
	width: 60px;
}
.search-block{
	width: 89%;
	height: 40px;
	background-color: #f9f9f9;
	float: left;
}
.icon{
	width: 25px;
	height: 25px;
	background-image: url(./icon.svg);
	float: left;
	margin: 8px; 
}
.search-full-icon{
	float: left;
	margin: 8px;
	width: 18%
}
.search-button{
	float: left;
	margin-left: 1%;
	width: 10%;
	height: 40px;
}
.search-block input{
	border: none;
	outline:none;
	width: 73%;
	height: 40px;
	float: left;
	background-color: #f9f9f9;
}
.menu{
	background-color: #e9e9e9; 
	padding: 10px;
	font-size: 12px;

}
.filter li{
	list-style-type: none;
}
.sort div{
	float: left;
	margin: 5px;
}
.purchase{
	border-top: solid 1px black;
	height: 160px;
}
.purchase-head{
	color: #248dd4;
}
.purchase-price{
	float: right;
}
.purchase-date{
	float: right;
}
.purchase-button-auction{
	color: #3781b3;
	width: 80%;
	float: left;
}
.purchase-text{
	width: 80%;
}
.search-status{
	height: 60px;
	padding: 10px;
}
.content{
	margin-left: 10px;
}
.logIn a{
	float: right;
	color: #fff;
	margin: 10px;
}
.avatar{
	width: 100px;
	height: 100px;
	border: solid 1px black;
	border-radius: 50px;
	float: left;
}
.personData{
	margin: 16px;
}
.personFIO{
	font-size: 20px;
	height: 100px;
}
.personInfo{
	
	padding: 28px;
}
.block{
	display:block;
}

.search-filter div{
	float: left;
	margin: 10px;
}
.document{
	background-color: #f0f0f0;
	margin: 16px;
	padding: 16px;
	height: 140px;
}
.document:hover{
	background-color: #e0e0e0;
}
.document-head{
	font-size: 20px;
	margin-bottom: 16px;
	font-family: serif;
}
.document-nomination{
	color: #a0a0a0;
	margin-bottom: 8px;
}
.add-document{
	text-align: center;
}
.add-document-button{
	border: solid black 1px;
	border-radius: 50px;
	width: 70px;
	height: 70px;
	margin: auto;
	margin-bottom: 16px;
}
.dropdown-procurment .dropdown-procurment-paragraph{
	background-color: #f0f0f0;
	margin: 16px;
	height: 32px;
	width: 400px;
	font-size: 20px;
}
.dropdown-procurment .dropdown-procurment-paragraph:hover{
	background-color: #e0e0e0;
}
.purchase-person-head{
	font-size: 32px;
}
.purchase-person-price{
	font-size: 23px;
	margin-bottom: 20px;
}
.purchase-person-town{
	margin-bottom: 20px;
}
.purchase-person-button{
	margin-bottom: 12px;
}

.content{	
	margin: auto;
	height: auto;
	overflow: hidden;
	width: 900px;
}
.purchase-info-card{
	margin: auto;
	width: 900px;
	margin-bottom: 24px;
	border: 1px solid #d9d9d9;
	border-bottom: 0px;
}
.purchase-info-card-head{
	color: #fff;
	background-color: #17313b;
	min-height: 38px;
	height: auto;
	font-size: 22px;
	padding-left: 12px;
	padding-top: 6px;
	border-bottom:  solid 1px #050b0d;
	font-family: 'Roboto Slab', serif;
}
.purchase-info-card-line{
	background-color: #f5f5f5; 
	min-height: 36px;
	height: auto;
	font-size: 14px;
	padding: 12px;
	margin: 6px;
	border-bottom: solid 1px #d9d9d9;
	margin: 6px;
	overflow: hidden;
	font-family: 'Roboto Slab', serif;
}
.card-info-name{
	float: left;
	width: 30%;
	font-weight: bold
	
}
.card-info{
	float: left;
	width: 70%;

}
.purchase-page-head{
	margin: auto;
	width: 900px;
	margin-bottom: 22px;
	margin-top: 22px;
	font-size: 28px;
	font-weight: thin;
	color: #555;

}
.purchase-page-navbar{
	width: 900px;
	height: 50px;
	background-color: #17313b;
	color: #fff;
	margin: auto;
	margin-top: 32px;
	margin-bottom: 32px;
}
.purchase-page-navbar a{
	float: left;
	width: 33.33333%;
	height: inherit;
	text-align: center;
	padding-bottom: 12px;
	padding-top: 5px;
	font-size: 22px;
	color: #fff;
}
.purchase-page-navbar a:hover{
	float: left;
	width: 33.33333%;
	height: inherit;
	padding-bottom: 12px;
	padding-top: 5px;
	color: #fff;
	text-decoration: none;
	border-bottom: 2px solid #f76969;
}
.purchase-page-navbar a:focus{
	text-decoration: none;
}
.purchase-page-navbar-active{
	border-bottom: 5px solid #f76969;
}
.purchase-page-navbar-active:hover{
	border-bottom: 7px solid #f76969;
}
.toggle-infoblock{
	width: 50px;
	height: 50px;
	display: none;
}
.purchase-one-info + input{
	display: none;
}
@media (max-width: 900px){
	.header-nav-button{
	display: block;
	width: 64px;
	height: 64px;
	background-image: url(./menu.svg);
	position: absolute;
	left: 0px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;

}
	.header-navbar{
		width: 250px;
		padding: 0px;
		position: relative;
}
	.header-navbar ul li{
		clear: left;
}
	.header-navbar ul{
		background-color: #0b181d;
		width: 0px;
		height: auto;
		overflow: hidden;
		position: absolute;
		opacity: 0; 
		-webkit-transition: .3s; 
		-o-transition: .3s; 
		transition: .3s;

	}
	.loginButton{
		width: 20%;
	}
	#header-nav-click:checked + label{
		left: 200px;
		-webkit-transition: .3s;
		-o-transition: .3s;
		transition: .3s;
	}
	#header-nav-click:checked + label + ul{
		width: 200px;
		opacity: 1;
		-webkit-transition: .3s;
		-o-transition: .3s;
		transition: .3s;
	}
	#login-window #login{
		width: 100%;
		padding: 5%;
	}
	.search{
	width: 100%;
	outline: none;
}
	.search-block{
		height: 30px;
		width: 80%;
	}
	.search-button{
		height: 30px;
		width: 65px;
	}
	.search-block input{
		width: 70%;
		height: 30px;
	}
	.search-full-icon{
		width: 50%;
		height: 30px;
		font-size: 12px;
		clear: left;
		position: relative;
		bottom: 8px;

	}
	.icon{
		width: 20px;
		height: 20px;
	}
		.content{	
		width: 100%;
		padding: none;
	}
	.header{
		display: none;
	}
	.purchase-info-card{
	margin: auto;
	width: 100%;
	margin-bottom: 24px;
	border: 1px solid #d9d9d9;
	border-bottom: 0px;
	padding: none;
	border: none;

}
.purchase-info-card-head{
	font-size: 14px;
	padding: 6px;
}
.purchase-info-card-line{
	margin: none;
	font-size: 11px;
	padding: 6px;
	
}
.purchase-one-info + input{
	display: none;
}
.toggle-infoblock{
	display:block;
	height: 20px;
	width: 20px;
	margin-right: 16px;
	background-image: url(./arrows.svg);
	float: right;
}
.purchase-one-infoblock{
	clear: left;
	overflow: hidden;
	height: 0px;
	-webkit-transition: height .5s;
	-o-transition: height .5s;
	transition: height .5s;
}
input:checked + label + .purchase-one-infoblock{
	height: 200px;
}
.purchase-one-price{
	width: 50%;

}
.purchase-one-date{
	width: 50%

}
.purchase-one-info{
	width: 100%;
}
.card-info-name{
	float: none;
	width: 80%;
	font-weight: bold
	
}
.card-info{
	float: none;
	width: 80%;
	margin-top: 8px;

}
.purchase-page-head{
	width: 100%;
}
.purchase-page-navbar{
	width: 100%;
}
.purchase-page-navbar a{
	font-size: 13px;
	padding: 6px;
	text-align: left;
}
.purchase-page-navbar a{
	font-size: 13px;
	padding: 6px;
	text-align: left;
}
.purchase-page-navbar-active{
	font-size: 13px;
}
.login-form{
	margin-top: 32px;
	margin-left: 0px; 
	background-color: #fefefe;
	width: 100%;
	border-right: none;
}
}



